<script>
import { ref } from "vue";
import { useCounterStore } from '../../store'
import { storeToRefs } from 'pinia';
import { useRouter } from 'vue-router'
import { toRaw } from'@vue/reactivity';
export default {
  setup() {
    let isCollapse = ref(false);
    let editableTabsValue = ref(0)
    const router = new useRouter
    let abc =  useCounterStore()
    let { tabsArr } = storeToRefs(abc)
    function checkByName(name2){
      
      let tabsArr = toRaw(abc.tabsArr)
      if (tabsArr.length>0) {
        for (let index = 0; index < tabsArr.length; index++) {
        if(name2 === abc.tabsArr[index].name){
          return false
        }
      }
      return  true
      } else {
        return true
      }
    }
    function addTabsArr(obj) {
      abc.$patch(state=>{
        if(checkByName(obj)){
          state.tabsArr.push({index:state.tabsArr.length,name:obj})
        }
      })
    }
    function goToIndex(el){
      router.replace(el.props.label)  
    }
    function removeByName(name1){
      for (let index = 0; index < abc.tabsArr.length; index++) {
        if(abc.tabsArr[index].name==name1){
          return index
        }
      }
    }
    function removeTab(al){
      abc.$patch(state=>{
        if(al == router.currentRoute.value.fullPath){
          return 
        } else {
        state.tabsArr.splice(removeByName(al),1)
        }
      })
    }
    function exit(){
      router.replace('/');
      localStorage.clear();
    }
    return {
      isCollapse,
      editableTabsValue,
      tabsArr,
      removeTab,
      addTabsArr,
      goToIndex,
      exit,
    };
  },
};
</script>
